/* Preview 스타일 */
.preview {
    margin: 20px;
    padding: 20px;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.preview_title {
    color: #333;
    font-size: 1.4em;
    margin-bottom: 10px;
}

.preview_title a {
    color: inherit;
    text-decoration: none;
    transition: color 0.3s;
}

.preview_title a:hover {
    color: #6ab04c;
}

.preview_wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* Content 스타일 */
.content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border-radius: 12px;
}

.name {
    font-size: 1.2em;
    font-weight: bold;
}

.cta {
    font-size: 0.9em;
    color: black;
    cursor: pointer;
    transition: color 0.3s;
}

.cta:hover {
    color: #f39c12;
}

/* 기본 스타일 */
.progress_wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #333;
    border-radius: 18px;
    padding: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    
    
    position: relative;
    overflow: hidden;
  }
  
  /* 애니메이션 효과 */
  .progress {
    background: linear-gradient(90deg, #00f260, #0575e6);
    width: 0%;
    height: 21.6px;
    border-radius: 18px;
    animation: progressAnimation 2s forwards;
  }
  
  @keyframes progressAnimation {
    0% {
      width: 0%;
    }
    100% {
      width: 20%;
    }
  }
  
  /* 진행 숫자 */
  .progress_number {
    margin-right: 10px;
    text-align: center;
    color: white;
    font-weight: bold;
    font-size: 1.2em;
    z-index: 1;
  }
  
