/* Participant Wrapper 스타일 */
.participant_wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.participant,
.complete_number {
    font-size: 0.9em;
    color: #555;
}

@media screen and (max-width: 570px){
    .preview_title{
        font-size: 1rem;
    }
    .name{
        font-size: .9rem;
    }
}